}
 */

.Layout {
  width: 80%;
  margin: 0% auto;
  background-color: #f3e9ce;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px 30px;
  border-bottom: 2px solid #145e69;
}
.logo img {
  width: 100%;
}
.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  font-size: 18px;
}
.contact :nth-child(1) {
  color: #3f787b;
}

@media only screen and (max-width: 600px) {
  .header {
    flex-direction: column;
  }
}

.banner img {
  width: 100%;
}
.flexArea {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 20px;
}
.lower-content {
  padding: 25px;
  text-align: left;
  text-align: initial;
}

@media only screen and (max-width: 768px) {
  .flexArea {
    flex-direction: column;
  }
}

h1 {
  color: #145e69;
}
.mainBody {
  display: flex;
  flex-direction: row;
  padding: 10px 20px;
}
.mainBody > .contentBody {
  flex: 2 1;
  text-align: left;
  text-align: initial;
  margin: 5px;
}
.mainBody > .contactForm {
  flex: 1 1;
  margin: 5px;
}
.contactForm {
  background-color: #145e69;
}

@media only screen and (max-width: 768px) {
  .mainBody {
    flex-direction: column;
  }
  .mainBody > .contentBody {
    flex: auto;
  }
  h1 {
    font-size: 1.5em;
  }
}

.contact-form-cls > form {
  display: flex;
  flex-direction: column;
}
.contact-form-cls {
  padding: 0px 20px;
  color: #fff;
}
form input {
  padding: 10px;
}
.note {
  padding-bottom: 10px;
}
form button {
  color: #fff;
  padding: 10px;
  font-weight: bold;
  background: -webkit-linear-gradient(
    top,
    #483d2e 0%,
    #483d2e 52%,
    #352a21 54%,
    #352a21 100%
  );
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#483d2e),
    color-stop(52%, #483d2e),
    color-stop(54%, #352a21),
    to(#352a21)
  );
  background: linear-gradient(
    to bottom,
    #483d2e 0%,
    #483d2e 52%,
    #352a21 54%,
    #352a21 100%
  );
}

@media only screen and (max-width: 768px) {
  .contact-form-cls {
    padding: 10px 20px;
  }
}

.card {
  padding: 20px;
  margin: 0px 5px;
}

@media only screen and (max-width: 768px) {
  .card {
    margin: 5px;
  }
}

.footer1 {
  display: flex;
  flex-direction: column;
  padding: 20px 0px;
  color: #fff;
  background-color: #352a21;
  text-transform: capitalize;
}
.footer-details,
.footer-nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 60%;
  margin: 0px auto;
}
.footer-details div,
.footer-nav div {
  padding: 0px 10px;
  text-align: center;
}
.footer-nav {
  padding-top: 20px;
}

.footer2 {
  color: #fff;
  background-color: #483e36;
  padding: 20px 0px;
}
.footer2 .content-1 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  width: 40%;
  margin: 0px auto;
  padding: 20px;
}
.footer2 div {
  padding: 0px 10px;
}
.content-2 {
  text-align: center;
  padding: 25px 0px 25px 0px;
}
a {
  color: inherit;
}
@media only screen and (max-width: 750px) {
  .footer-details,
  .footer-nav {
    width: 100%;
    box-sizing: border-box;
  }
  .footer2 .content-1 {
    width: 100%;
    box-sizing: border-box;
  }
}

